<template>
  <div id="app">
    <div class="plant-tree">
      <avatar :user="user"/>
      <div class="clearfix"></div>
      <tree :treeLevel="user.treeLevel"/>
      <tool-bar/>
      <div class="clearfix"></div>
    </div>

    <div class="jump">
      <button>查看排行榜</button>
    </div>

    <prop :power="power"/>
  </div>
</template>

<script>
import Avatar from "./components/Avatar"
import Tree from './components/Tree'
import ToolBar from "./components/ToolBar";
import Prop from './components/Prop'

export default {
  name: 'app',
  components: {
    Avatar, Tree, ToolBar, Prop,
  },
  data() {
    return {
      user: {
        name: 'name',
        level: '透亮会员',
        treeLevel: '2000',
        avatar: '../assets/avatar.png'
      },
      props: {
        water: '3',
        fertilizer: '2',
        sun: '1',
        propMax: '5',
      },
      power: '200'
    }    
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
}  
#app {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}
.plant-tree {
  background-image: url('./assets/bg.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 85%;
  overflow:hidden;
  position: relative;
}
.clearfix {
  clear: both;
}
.tree {
  margin: 80px 0px 0px;
}
.tool-bar {
      width: 95%;
    margin-left: 2%;
      position: absolute;
    bottom: 7px;
    width: 100%;
}
.jump {
  text-align: center;
  margin: 30px;
}
button {
  font-size: 20px;
  background-image: url("./assets/dialog-2.png");
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 10px 30px;
  -webkit-transition: .2s;
  -moz-transition: .2s;
  transition: .2s;
}
button:hover {
  padding: 12px 35px;
}
</style>



