<template>
    <div class="tool-bar">
        <div class="tool-left">
            <div class="item" @click="prop()">
                <img class="f-img" src="../assets/raider.png">
                攻略
            </div>

            <div class="item">
                <img class="f-img" src="../assets/tool.png">
                道具屋
            </div>
        </div>

        <div class="tool-right">
            <div class="item">
                <img class="r-img" src="../assets/water.png">
                水分
                <img class="watering" src="../assets/watering.png">
                <div class="weight">
                    +20g
                </div>
            </div>

            <div class="item">
                <img class="r-img" src="../assets/fertilizer.png">
                施肥
                <div class="weight">
                    +30g
                </div>
            </div>

            <div class="item">
                <img class="r-img" src="../assets/sun.png">
                阳光
                <div class="weight">
                    +40g
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                wActive: false,
                fActive: false,
                sActive: false,
            }
        }
    }
</script>

<style scoped>
    @keyframes watering{
        0% {opacity: 0; top: -100px;}
        50% {opacity:1; top: -180px;}
        100% {opacity: 0;}
    }

    @keyframes weight{
        0% {opacity:0; top:0px;}
        40% {opacity:1; top: -200px;}
        60% {opacity:1; top: -200px;}
        100%{top: -600px; opacity: 0px;}
    }

    .tool-left {
        display: inline;
        float: left;
            margin-left: 15px;
    }
    .tool-right {
        display: inline;
        float: right;
        bottom: 0px;
        height: 90px;
        margin-right: 25px;
    }
    .item {
        display: inline-block;
        text-align: center;
        color: #fff;
        font-size: 12px;
        margin: 10px 10px 10px 0;
        position: relative;
    }
    img {
        display: block;
    }
    .f-img {
        width: 40px;
        -webkit-transition: .2s;
        -moz-transition: .2s;
        transition: .2s;
    }
    .f-img:hover {
        width: 55px;
    }
    .r-img {
        width: 35px;
        margin-top: 10px;
        -webkit-transition: .2s;
        -moz-transition: .2s;
        transition: .2s;
    }
    .r-img:hover {
        width: 45px;
    }
    .watering {
        position: absolute;
        width: 150px;
        top: -100px;
        opacity: 0;
    }
    .item:hover .watering {
        animation: watering 2s;
    }
    .weight {
        position: absolute;
        background-color: #00baff;
        border: #fff solid 2px;
        width: 30px;
        height: 30px;
        border-radius: 50% 50%;
        line-height: 30px;
        opacity: 0;
    }
    .item:hover .weight {
        animation: weight 3s;
        animation-timing-function: ease;
    }
</style>


