<template>
    <div class="prop" :class="{'close': isClose}">
        <div class="prop-room">
            <div class="prop-head">   
                <span>道具屋</span>
                <br>
                （目前拥有{{power}}点肾能量）
                <div class="close-button" @click="close()">&times;</div>
                <hr>
            </div>

            <div class="prop-body">
                <div class="alarm">{{alarm}}</div>
                
                <div class="item">
                    <span>小小水滴</span>
                    <div class="illustrate">
                        增加20g树木重量
                    </div>
                    <img src="../assets/water.png">
                </div>

                <div class="item">
                    <span>化肥</span>
                    <div class="illustrate">
                        增加30g树木重量
                    </div>
                    <img src="../assets/fertilizer.png">
                </div>

                <div class="item">
                    <span>阳光</span>
                    <div class="illustrate">
                        增加40g树木重量
                    </div>
                    <img src="../assets/sun.png">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['display', 'props', 'power'],
        data() {
            return {
                isClose: false,
                alarm: '对不起',
            }
        },
        methods: {
            close: function () {
                this.isClose = true;
            }
        }
    }
</script>

<style scoped>
    .prop {
        background-color: rgba(0, 0, 0, 0.4);
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        z-index: 999;
        text-align: center;
    }
    .close {
        display: none;
    }
    .prop-room {
        height: 40%;
        width: 90%;
        margin: 180px auto 0px;
        border-radius: 20px 20px;
        background-color: #fff;
        font-size: 12px;
        overflow: hidden;
    }
    .prop-head {
        color: #5aab26;
        position: relative;
    }
    .prop-head span {
        font-size: 20px;
        font-weight: 500;
    }
    .prop-body {
        width: 100%;
        height: 100%;
        background-color: #ddd;
        border-radius: 0 0 20px 20px;
    }
    .close-button {
        font-size: 20px;
        width: 20px;
        height: 20px;
        margin: 10px;
        border: 2px #5aab26 solid;
        border-radius: 50% 50%;
        position: absolute;
        right: 0px;
        top: 0px;
    }
    hr {
        margin: 5px auto 0;
        width: 15%;
        border: 2px #5aab26 solid;
        position: relative;
        top: 2px;
    }
    .alarm {
        height: 12px;
        font-size: 12px;
        color: red;
    }
    .item {
        height: 80%;
        width: 28%;
        margin: 5px calc(7% / 6);
        border-radius: 5px 5px;
        padding: 3px;
        background-color: #fff;
        display: inline-block;
    }
    .item span {
        color: #000;
        font-size: 18px;
        display: inline;
    }
    .illustrate {
        color: #5aab26;
        font-size: 12px;
        border: #5aab26 solid 1px;
    }
    img {
        width: 60%;
    }
</style>


